import {Component, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {DatePipe, Location} from '@angular/common';
import {ActivatedRoute, Router} from '@angular/router';
import {Events, ModalController, Platform} from '@ionic/angular';
import {LoaderService} from '../../services/loader.service';
import {Storage} from '@ionic/storage';
import {SermonService} from '../../services/sermon.service';
import {DomSanitizer} from '@angular/platform-browser';
import {ToastService} from '../../services/toast.service';
import {CommonService} from '../../services/common.service';
import {ApiService} from '../../services/api.service';
import {QrScannerComponent} from 'angular2-qrscanner';
import {QrcodeModalComponent} from './qrcode-modal/qrcode-modal.component';
import {IdService} from '../../services/id.service';
import {environment} from "../../environments/environment";
import {TranslateService} from "@ngx-translate/core";

@Component({
    selector: 'app-check-in',
    templateUrl: './check-in.page.html',
    styleUrls: ['./check-in.page.scss'],
    encapsulation: ViewEncapsulation.None,
    providers: [DatePipe, QrcodeModalComponent]

})
export class CheckInPage implements OnInit {
    @ViewChild(QrScannerComponent, {static: false}) qrScannerComponent: QrScannerComponent;

    ch_id: any;

    banner_image: any;
    banner_desc: any;
    banner_title: any;

    page_name: any;
    checkin_detail: any;
    checkin_time: any;
    people: any;
    search_input: any;
    filtered_people: any;
    camera: boolean = false;

    show_create_fields: boolean = false;

    // create profile
    name: any;
    phone: any;
    email: any;
    gender: any;
    dob: any;
    life_stage: any;
    membership: any;
    is_volunteer: any;
    confirmation_user_type: any;
    household_user_type: any;
    interested_volunteer: any;
    confirmation: any;
    confirmation_checkin_toggle: boolean = true;
    household_checkin_toggle: any = [];
    audience_options: any;
    add_family: boolean = false;
    name_for_relation: any;
    household: any = [];
    relation: any;
    checkin_church_id: any;
    time: any;
    existing_family_members: any;

    scan_done: boolean = true;
    alertMessage: any;
    constructor(
        private platform: Platform,
        private route: ActivatedRoute,
        private loader: LoaderService,
        private storage: Storage,
        private sermonsService: SermonService,
        private dom: DomSanitizer,
        private router: Router,
        private location: Location,
        private common: CommonService,
        private toast: ToastService,
        private api: ApiService,
        private scannerModal: QrcodeModalComponent,
        public modalController: ModalController,
        private idService: IdService,
        private translate: TranslateService,
    ) {

        // get translated alert message
        let interval = setInterval(() => {
            this.translate.get('ALERTS').subscribe( (res: any) => {
                this.alertMessage = res;
                clearInterval(interval);
            });
        }, 2000)
    }

    async ngOnInit() {

        let camp_id = this.route.snapshot.paramMap.get('id');
        let link_code = this.route.snapshot.paramMap.get('link_code');
        this.ch_id = this.route.snapshot.paramMap.get('ch_id');

        if (!this.ch_id) {
            this.ch_id = environment.church_id;
        }


        this.idService.setId(this.ch_id);

        this.getCheckInData(camp_id, link_code);

        // this.launcgQrScanner();

    }

    async openModal() {
        let modal;
        modal = await this.modalController.create({
            component: QrcodeModalComponent,
            cssClass: 'scannerModal',
            componentProps: {}
        });


        modal.onDidDismiss().then(res => {

        });

        return await modal.present();

    }


    launcgQrScanner() {
        this.qrScannerComponent.getMediaDevices().then(devices => {
            const videoDevices: MediaDeviceInfo[] = [];
            for (const device of devices) {
                if (device.kind.toString() === 'videoinput') {
                    videoDevices.push(device);
                }
            }
            if (videoDevices.length > 0) {
                let choosenDev;
                for (const dev of videoDevices) {
                    if (dev.label.includes('front')) {
                        choosenDev = dev;
                        break;
                    }
                }
                if (choosenDev) {
                    this.qrScannerComponent.chooseCamera.next(choosenDev);
                } else {
                    this.qrScannerComponent.chooseCamera.next(videoDevices[0]);
                }
            }
        });

        this.qrScannerComponent.capturedQr.subscribe(result => {
            if (result) {
                this.loadProfileQR(result);
            }
            this.camera = !this.camera;
        });
    }

    getCheckInData(id, link_code) {
        this.api.get_checkin(id, link_code).subscribe(async (res: any) => {
            if (res.status) {
                this.checkin_detail = res.checkin;
                this.checkin_time = res.checkin.times;
                this.checkin_time = JSON.parse(this.checkin_time);
                this.checkin_church_id = res.checkin.church_id;
                this.audience_options = JSON.parse(res.checkin.audience_options);
                this.getPeople(res.checkin.church_id);
            }
        }, (err: any) => {
            let message = err.message;
            this.loader.stopLoading();
            this.toast.presentToastWithOptions(message, (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
        });
    }

    getPeople(church_id) {
        this.api.get_people(church_id).subscribe(async (res: any) => {
            this.people = res.people;
        }, (err: any) => {
            let message = err.message;
            this.loader.stopLoading();
            this.toast.presentToastWithOptions(message, (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
        });
    }

    filter_people() {
        if (this.search_input.length !== 0) {
            this.filtered_people = this.people;
        } else {
            this.filtered_people = [];
        }
    }

    set_confirmation(obj) {
        if (this.confirmation && obj.first_last_name === this.confirmation.first_last_name) {
            this.confirmation = obj;
            this.filtered_people = [];
            this.confirmation_user_type = this.audience_options[0].name;
        } else {
            this.confirmation = obj;
            this.confirmation['checkin_toggle'] = 1;
            this.confirmation['type'] = '';

            this.filtered_people = [];
            this.household = [];

            // getting its family members
            const body = JSON.stringify({
                //people_id: 2008,
                people_id: this.confirmation.id,
            });
            this.api.get_household(body).subscribe((res: any) => {
                if (res.status) {
                    this.household = res.family;

                    for (let i = 0; i < this.household.length; i++) {
                        this.household[i]['checkin_toggle'] = 0;
                        this.household[i]['type'] = '';
                        this.household[i]['relation_name'] = this.household[i].relation.name;
                    }

                }
            });

            this.confirmation_user_type = this.audience_options[0].name;
        }

        this.name_for_relation = this.confirmation.first_last_name;


    }

    createUser() {
        this.show_create_fields = !this.show_create_fields;
        if (this.add_family) {
            let rel = this.relation.split('+');

            let familyUserData = {
                first_last_name: this.name,
                life_stage: this.life_stage,
                gender: this.gender,
                phone: this.phone,
                email: this.email,
                dob: this.dob,
                membership: this.membership,
                is_volunteer: this.is_volunteer,
                interest_volunteer: this.interested_volunteer,
                relation_id: rel[1],
                relation_name: rel[0],
                checkin_toggle: 0,
                type: ''
            };
            this.household.push(familyUserData);
        } else {
            let userData = {
                name: this.name,
                life_stage: this.life_stage,
                gender: this.gender,
                phone: this.phone,
                email: this.email,
                dob: this.dob,
                membership: this.membership,
                is_volunteer: this.is_volunteer,
                interest_volunteer: this.interested_volunteer,
            };
        }

        this.createPerson();
    }

    createPerson() {
        const body = JSON.stringify({
            church_id: this.checkin_detail.church_id,
            campus_id: this.checkin_detail.campus_id,
            checkin_id: this.checkin_detail.id,
            first_last_name: this.name,
            life_stage: this.life_stage,
            gender: this.gender,
            phone: this.phone,
            email: this.email,
            birthday: this.dob,
            membership: this.membership,
            is_volunteer: this.is_volunteer,
            interest_volunteer: this.interested_volunteer,
            create_household: this.add_family
        });


        this.api.create_person(body).subscribe((res: any) => {
            if (res.status) {
                this.ngOnInit();

                // clearing input data
                this.name = '';
                this.phone = '';
                this.email = '';
                this.gender = '';
                this.dob = '';
                this.life_stage = '';
                this.membership = '';
                this.is_volunteer = '';

                this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.PersonSuccessfully : 'Person Created Successfully'), (this.alertMessage ? this.alertMessage.Success : 'Success'), 'success');
            }
        });
    }

    checkin() {

        let checked = {
            id: this.confirmation.id,
            type: this.confirmation.type
        };

        let time;
        if (this.checkin_detail.times) {
            time = JSON.stringify(this.checkin_detail.times);
        }

        const body = JSON.stringify({
            checkin_id: this.checkin_detail.id,
            time: time[0],
            people_checked: checked,
            church_id: this.checkin_detail.church_id,
            require_checkout: this.checkin_detail.require_checkout
        });

        // this.api.checkin(body).subscribe((res: any) => {
        //     if (res.status){
        //         this.toast.presentToastWithOptions('Checked In Successfully', 'Success', 'success');
        //     }
        // });

        // if (this.time) {
        //     if (this.confirmation || this.household) {
        //         this.toast.presentToastWithOptions('Person Checked In Successfully', 'Success', 'success');
        //     }
        // } else {
        //     this.toast.presentToastWithOptions('Select Time', 'Warning', 'warning');
        // }
    }

    loadProfileQR(qr_id) {
        const body = JSON.stringify({
            checkin_id: this.checkin_detail.id,
            qr_id: qr_id
        });

        this.api.load_profileQR(body).subscribe((res: any) => {
            if (res.status) {

                this.confirmation = res.data;

                this.scan_done = false;
            }
        });

    }

    gotobackPage() {
        this.location.back();
    }

}
